import React, { Component } from "react";
import { QueryRenderer, graphql } from "react-relay";
import { useEnvironment } from "@/src/relay/init";
import Hello from "@/src/relay/fragments/Hello";

const helloQuery = graphql`
  query HelloQuery {
    ...HelloFragment
  }
`;

export default function HelloQuery() {
  const environment = useEnvironment();
  return (
    <QueryRenderer
      environment={environment}
      query={helloQuery}
      render={({ error, props }) => {
        if (error) {
          return <div>am universe: {error.message}</div>;
        } else if (props) {
          return <Hello rt={props} />;
        }
        return <div>Loading</div>;
      }}
    />
  );
}
